'use client';
import { motion } from 'framer-motion';
import React from 'react';
import HeroSection from './HeroSection';


function Card({ title, description, children }: any) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 40 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
      className="group relative w-full md:w-[40%] min-h-[270px] bg-gradient-to-b from-[#0B1627] to-[#0D1B2A] p-4 rounded-3xl border border-[#223347] shadow-xl overflow-visible"
    >
      {/* Hover glow effect */}
      <div className="absolute inset-0 bg-gradient-to-r from-cyan-400/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-700 pointer-events-none" />
      <div className="absolute top-0 left-0 w-full h-full rounded-3xl border border-transparent group-hover:border-cyan-400 transition-all duration-700 animate-border-glow" />

      <h3 className="text-white text-lg font-bold mb-1 z-10 relative">{title}</h3>
      <p className="text-sm text-gray-300 mb-2 z-10 relative">{description}</p>
      {children}
    </motion.div>
  );
}


