import { motion } from 'framer-motion';
import { Meteors } from '@/components/ui/meteor'; // adjust path if needed

export default function HeroSection() {
  return (
    <section className="relative w-full h-screen text-white font-inter overflow-hidden flex items-center justify-center px-4">
      
      {/* Meteors in background */}
      <Meteors number={30} className="z-0" />

      <div className="relative z-10 text-center max-w-3xl">
        <div className="mb-6 flex justify-center">
          <img src="/icons/logo.svg" alt="Majarah Logo" className="w-32" />
        </div>

        <h1 className="text-4xl sm:text-5xl md:text-6xl font-semibold leading-tight">
          Don‚Äôt <span className="text-[#3ABEFF]">Blend in</span><br />
          Go <span className="text-[#3ABEFF]">Impact</span> with <span className="text-[#3ABEFF]">Us</span>.
        </h1>

        <motion.a
          whileHover={{ scale: 1.05 }}
          href="#contact"
          className="inline-block mt-8 px-6 py-3 bg-gradient-to-r from-[#2A2B5F] to-[#3ABEFF] text-white rounded-full shadow-md hover:shadow-lg transition-all duration-300"
        >
          Contact Us Today ‚Üí
        </motion.a>
      </div>
    </section>
  );
}
